<script>
import Vue from 'vue'
import { Icon, Switch, Toast, Uploader } from 'vant'
import { userInfo, uploadImg, avatarImg } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
// import Cryptojs from 'crypto-js'
import NavHead from '@/components/head.vue'
Vue.use(Icon).use(Switch).use(Uploader).use(Toast)
export default {
  components: { NavHead },
  data () {
    return {
      languageStatus: '',
      information: '',
      url: this.GLOBAL.domainName,
      title: '个人中心',
      parameter: {
        u_id: ''
      },
      avatarImgData: { u_id: '', avatar: '' }
    }
  },
  created () {
    console.log('1111111')
    this.parameter.u_id = sessionStorage.getItem('u_id')
    this.avatarImgData.u_id = sessionStorage.getItem('u_id')
    this.user()
    this.getLanguageStatus()
  },
  methods: {
    getLanguageStatus () {
      console.log(this.$store.state.language)
      this.languageStatus = this.$store.state.language
    },

    afterRead1 (file) {
      var that = this
      var fd = new FormData()
      fd.append('file111', file.file)
      uploadImg(fd).then(res => {
        console.log(res)
        if (res.data === '上传失败') {
          that.$toast({
            message: res.data,
            duration: 1500
          })
        } else {
          that.information.avatar = res.data
          that.avatarImgFun()
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    avatarImgFun () {
      var that = this
      that.avatarImgData.avatar = that.information.avatar
      var data = objKeySort(that.avatarImgData)
      var token = sgin(data)
      that.avatarImgData.token = token
      avatarImg(that.avatarImgData).then(res => {
        if (res.code === 0) {
          that.$toast({
            message: '修改头像成功',
            duration: 1500
          })
          delete that.avatarImgData.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        delete that.avatarImgData.token
      })
    },
    user () {
      var data = objKeySort(this.parameter)
      var token = sgin(data)
      this.parameter.token = token
      var that = this
      userInfo(this.parameter).then(res => {
        console.log(res, '用户信息')
        that.information = res.data
      }).catch((err) => console.log(err))
    },
    add0 (m) { return m < 10 ? '0' + m : m },
    format (isTime) {
      var times = isTime * 1000
      var time = new Date(times)
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      // var h = time.getHours()
      // var mm = time.getMinutes()
      // var s = time.getSeconds()
      // console.log(y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s))
      return y + '-' + this.add0(m) + '-' + this.add0(d)
    },
    toAuthentication (authenticate) {
      var that = this
      this.$router.push({
        path: '/personal/authentication',
        query: {
          authenticate: authenticate,
          avatar: that.information.avatar,
          username: that.information.username,
          login_time: that.information.login_time
        }
      })
    },
    toBankcid () {
      this.$router.push({
        path: '/personal/bankcidList',
        query: {
        }
      })
    },
    toCertification () {
      this.$router.push({
        path: '/personal/certification',
        query: {
        }
      })
    },
    toSetup () {
      this.$router.push({
        path: '/personal/setUp',
        query: {
        }
      })
    },
    toCustomer () {
      this.$router.push({
        path: '/personal/customer',
        query: {
        }
      })
    },
    toSecurity () {
      this.$router.push({
        path: '/personal/security',
        query: {
        }
      })
    },
    toOrder () {
      this.$router.push({
        path: '/personal/order',
        query: {
          //
          // bibi: ''
        }
      })
    },
    toInvitaion () {
      this.$router.push({
        path: '/personal/invitation',
        query: {
        }
      })
    },
    toAddressList () {
      //  addressList addressDel
      this.$router.push({
        path: '/personal/addressList',
        query: {
        }
      })
    },
    tonotice () {
      this.$router.push({
        path: '/notice',
        query: {
        }
      })
    }
  }
}
</script>
